  font-family: 'Great Vibes';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/greatvibes/v15/RWmMoKWR9v4ksMfaWd_JN-XC.ttf) format('truetype');
}
@font-face {
  font-family: 'Great Vibes';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/greatvibes/v15/RWmMoKWR9v4ksMfaWd_JN-XC.ttf) format('truetype');
}
@font-face {
  font-family: 'Monsieur La Doulaise';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/monsieurladoulaise/v15/_Xmz-GY4rjmCbQfc-aPRaa4pqV340p7EZl5e.ttf) format('truetype');
}
@font-face {
  font-family: 'Pinyon Script';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/pinyonscript/v18/6xKpdSJbL9-e9LuoeQiDRQR8aOI.ttf) format('truetype');
}
.mainContainer {
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out;
}
.mainContainer .noisyBackGround {
  position: fixed;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  width: 200%;
  height: 200vh;
  background: transparent url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 50%;
  animation: bg-animation 0.2s infinite;
  opacity: 0.9;
  visibility: hidden;
}
@keyframes bg-animation {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -5%);
  }
  20% {
    transform: translate(-10%, 5%);
  }
  30% {
    transform: translate(5%, -10%);
  }
  40% {
    transform: translate(-5%, 15%);
  }
  50% {
    transform: translate(-10%, 5%);
  }
  60% {
    transform: translate(15%, 0);
  }
  70% {
    transform: translate(0, 10%);
  }
  80% {
    transform: translate(-15%, 0);
  }
  90% {
    transform: translate(10%, 5%);
  }
  100% {
    transform: translate(5%, 0);
  }
}
.mainContainer .image-container {
  cursor: grab;
}
.mainContainer .image-container #image-track {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(0%, -50%);
  user-select: none;
  /* -- Prevent image highlighting -- */
}
.mainContainer .image-container #image-track .imageContainer {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.mainContainer .image-container #image-track .imageContainer .image {
  width: 37vmin;
  height: 55vmin;
  margin-left: 2vmin;
  margin-right: 2vmin;
  opacity: 1;
  object-fit: cover;
  object-position: 100% center;
  transition: width 0.6s ease, opacity 0.6s ease, margin-left 0.6s ease, margin-right 0.6s ease;
}
.mainContainer .image-container #image-track .imageContainer .imageLoading {
  color: white;
  width: 37vmin;
  height: 55vmin;
  margin-left: 2vmin;
  margin-right: 2vmin;
}
.mainContainer .image-container #image-track .imageContainer .imageSensible {
  width: 0;
  opacity: 0;
  margin-left: 0;
  margin-right: 0;
}
.mainContainer .imageDescription {
  width: 100vw;
  position: absolute;
  right: 0;
  bottom: 5vh;
  text-align: center;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 5px;
}
.mainContainer .imageDescription .imageDescriptionSeparator {
  opacity: 0.2;
}
.mainContainer .imageDescription .imageDescriptionInfoOpen {
  opacity: 0.2;
  padding-top: 10px;
  letter-spacing: 2px;
}
